
#app{
  width: 100%;
  height: 100%;
}
#view{
  position: absolute;
  top: 50px;
  left: 0px;
  width: 100vw;
  height: calc(100vh - 50px);
  overflow: auto;
  background-image: url("./assets/back.png");
  text-align: center;
  padding: 20px;
  border-style: solid;
  border: #24292E;
}
.fade-enter-active, .fade-leave-active {
  -webkit-transition: opacity .5s;
  transition: opacity .5s
}
.fade-enter, .fade-leave-to {
  opacity: 0
}




